<div class="fab-fixed-wrap with-navbar-bottom" style="bottom: 4.6875rem;position: fixed;z-index: 1035;right: .9375rem;bottom: .9375rem;">
  <a href="#" target="_blank" class="fab fab-facebook mp-click mini-cart-icon-right"  @click.prevent="goToOrder()">
    <i class="fas fa-shopping-cart fa-lg" style="color: #009A86"></i>
    <span v-if="cart.carts && cart.carts.length > 0 ">{{ cart.carts.length }}</span>
  </a>
</div>
</div>
</template>



<script>
import $ from "jquery";

export default {
  data() {
    return {
      products: [],
      product: {},
      cart: [],
      user:{
        username:'',
        password:'',
      },
      form:{
        user:{
          name:'',
          email:'',
          tel:'',
          address:'',
        },
        mseeage:'',
      },
      status: {
        loadingItem: ""
      },
      isLoading: false
    };
  },
  methods: {
    getProducts() {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/products`;
      vm.isLoading = true;
      this.$http.get(url).then(response => {
        console.log("get products", response);
        vm.products = response.data.products;
        vm.isLoading = false;
      });
    },
    getProduct(id) {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/product/${id}`;
      console.log(url);
      vm.status.loadingItem = id;
      this.$http.get(url).then(response => {
        vm.product = response.data.product;
        $("#productModal").modal("show");
        console.log(response);
        vm.status.loadingItem = "";
      });
    },
    addtoCart(id, qty = 1) {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/cart`;
      vm.status.loadingItem = id;
      const cart = {
        product_id: id,
        qty
      };
      //符合api
      this.$http.post(url, { data: cart }).then(response => {
        console.log("addtoCart", response);
        vm.status.loadingItem = "";
        vm.getCart();
        $("#productModal").modal("hide");
      });
    },
    getCart() {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/cart`;
      vm.isLoading = true;
      this.$http.get(url).then(response => {
        // vm.products = response.data.products;
        vm.cart = response.data.data;
        console.log("getCart", response.data.data.carts);
        vm.isLoading = false;
      });
    },
    removerCart(id) {
      console.log("removeCart", id);
      //刪除一樣id的物件
      const vm = this;
      const url = `${process.env.VUE_APP_API}/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(url).then(response => {
        console.log("removerCart", response);
        this.getCart();
        vm.isLoading = false;
      });
    },
    goToOrder() {
      $("#orderModal").modal("show");
      $('[data-toggle="tooltip"]').tooltip();
    },
    creatOrder() {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/order`;
      const order = vm.form;
      // vm.isLoading = true;
        this.$validator.validateAll().then((result) => {
        if (result) {
           this.$http.post(url,{data:order}).then((response) => {
            console.log('訂單已建立',response);
            if (response.data.success) {
              vm.$router.push(`/checkout/${response.data.orderId}`);
            }
            vm.isLoading = false;
          });
        }else{
          console.log('欄位不完整')
        }
        });
    },
    signin() {
        const api = `https://vue-course-api.hexschool.io/admin/signin`;
        console.log('api url',api)
        const vm = this;
        this.$http.post(api, vm.user).then((response) => {
          console.log('signin()',response.data);
          if (response.data.success) {
            vm.$router.push('/admin');
          }
        });
      },
    signout() {
      const vm = this;
      const url = `https://vue-course-api.hexschool.io/logout`;
      this.$http.post(url).then((response) => {
          console.log(response.data)
        if (response.data.success) {
          vm.$router.push('/');
        }
      });
    },
  },
  created() {
    this.getProducts();
    this.getCart();
  }
};
</script>

