<template>
    <div>
         <loading :active.sync="isLoading" ></loading>
       <div class="text-right mt-4">
           <button class="btn btn-primary" @click="openModal(true)">
            建立新產品</button>
            <table class="table mt-4">
                <thead>
                    <tr>
                        <th width="120">分類</th>
                        <th>產品名稱</th>
                        <th width="120">原價</th>
                        <th width="120">售價</th>
                        <th width="100">是否啟用</th>
                        <th width="80">編輯</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item) in products" :key="item.id">
                        <td>{{item.category}}</td>
                        <td>{{item.title}}</td>
                        <td class="text-right">
                            {{item.origin_price| currency}}
                        </td>
                        <td class="text-right">
                            {{item.price}}
                        </td>
                        <td>
                            <span v-if="item.is_enabled" class="text-success">啟用</span>
                            <span v-else>未啟用</span>
                        </td>
                        <td>
                            <button class="btn btn-outline-primary btn-sm" @click="openModal(false,item)">編輯</button>
                            <button class="btn btn-outline-danger btn-sm" @click="opendelProductModal(item)">刪除</button>
                        </td>
                    </tr>
                </tbody>
            </table>
            <!--pagination-->
            <!-- <nav aria-label="Page navigation example">
                <ul class="pagination">
                    <li class="page-item" :calss="{'disabled' : !pagination.has_pre}">
                        <a class="page-link" href="#" aria-label="Previous" @click.prevent = "getProducts(pagination.current_page - 1)">
                        <span aria-hidden="true">&laquo;</span>
                        </a>
                    </li>
                    <li class="page-item" v-for="page in pagination.total_pages" :key="page" 
                    :class="{'active' : pagination.current_page === page}">
                    <a class="page-link" href="#" @click.prevent = "getProducts(page)">{{page}}</a></li>
                    <li class="page-item" :calss="{'disabled' : !pagination.has_next}">
                        <a class="page-link" href="#" aria-label="Next" @click.prevent = "getProducts(pagination.current_page + 1)">
                        <span aria-hidden="true">&raquo;</span>
                        </a>
                    </li>
                </ul>
            </nav> -->
            <Pagination :pages="pagination" @emitPages="getProducts"></Pagination>

           
            <!-- Modal -->
            <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
                aria-hidden="true">
                <div class="modal-dialog modal-lg" role="document">
                    <div class="modal-content border-0">
                        <div class="modal-header bg-dark text-white">
                            <h5 class="modal-title" id="exampleModalLabel">
                                <span>新增產品</span>
                            </h5>
                            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div class="modal-body">
                            <div class="row">
                                <div class="col-sm-4">
                                    <div class="form-group">
                                        <label for="image">輸入圖片網址</label>
                                        <input type="text" class="form-control" id="image" placeholder="請輸入圖片連結" v-model="tempProduct.imageUrl">
                                    </div>
                                    <div class="form-group">
                                        <label for="customFile">或 上傳圖片
                                            <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                                        </label>
                                        <input type="file" id="customFile" class="form-control" ref="files" @change="uploadFile">
                                    </div>
                                   <img class="img-fluid" :src="tempProduct.imageUrl" alt="">
                                </div>
                                <div class="col-sm-8">
                                    <div class="form-group">
                                        <label for="title">標題</label>
                                        <input type="text" class="form-control" id="title" placeholder="請輸入標題" v-model="tempProduct.title">
                                    </div>

                                    <div class="form-row">
                                        <div class="form-group col-md-6">
                                            <label for="category">分類</label>
                                            <input type="text" class="form-control" id="category" placeholder="請輸入分類"  v-model="tempProduct.category">
                                        </div>
                                        <div class="form-group col-md-6">
                                            <label for="price">單位</label>
                                            <input type="unit" class="form-control" id="unit" placeholder="請輸入單位" v-model="tempProduct.unit">
                                        </div>
                                    </div>

                                    <div class="form-row">
                                        <div class="form-group col-md-6">
                                            <label for="origin_price">原價</label>
                                            <input type="number" class="form-control" id="origin_price" placeholder="請輸入原價" v-model="tempProduct.origin_price">
                                        </div>
                                        <div class="form-group col-md-6">
                                            <label for="price">售價</label>
                                            <input type="number" class="form-control" id="price" placeholder="請輸入售價" v-model="tempProduct.price">
                                        </div>
                                    </div>
                                    <hr>

                                    <div class="form-group">
                                        <label for="description">產品描述</label>
                                        <textarea type="text" class="form-control" id="description"
                                            placeholder="請輸入產品描述" v-model="tempProduct.description"></textarea>
                                    </div>
                                    <div class="form-group">
                                        <label for="content">說明內容</label>
                                        <textarea type="text" class="form-control" id="content" placeholder="請輸入產品說明內容"  v-model="tempProduct.content"></textarea>
                                    </div>
                                    <div class="form-group">
                                        <div class="form-check">
                                            <input class="form-check-input" type="checkbox" id="is_enabled"  v-model="tempProduct.is_enabled"
                                            :true-value="1" :false-value="2">
                                            <label class="form-check-label" for="is_enabled">
                                                是否啟用
                                            </label>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                            <button type="button" class="btn btn-primary" @click="updateProduct">確認</button>
                        </div>
                    </div>
                </div>
            </div>
            <!-- delete product modal-->
            <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
             aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div class="modal-dialog" role="document">
                    <div class="modal-content border-0">
                        <div class="modal-header bg-danger text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span>刪除產品</span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                        </div>
                        <div class="modal-body">
                            是否刪除 <strong class="text-danger">{{ tempProduct.title }}</strong> 商品(刪除後將無法恢復)。
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                            <button type="button" class="btn btn-danger" @click="deleteProduct()">確認刪除</button>
                        </div>
                    </div>
                </div>
             </div>
       </div>
    </div>
</template>

<script>
import $ from 'jquery';
import Pagination from '../Pagination';
export default {
  data() {
    return {
      products: [],
      tempProduct:{},
      pagination:{},
      isNew:false,
      isLoading:false,
      status:{
          fileUploading:false,
      }
    };
  },
  components: {
    Pagination,
  },
  methods: {
    getProducts(page = 1) {
        const api = `${process.env.VUE_APP_API}/products?page=${page}`;
        const vm = this;
        vm.isLoading = true;
        this.$http.get(api).then((response) => {
            vm.products = response.data.products;
            vm.isLoading = false;
            vm.pagination = response.data.pagination
        })
        
    },
    openModal(isNew,item){
        if(isNew){
            this.tempProduct = {};
            this.isNew = true ; 
        }else{
            this.tempProduct = Object.assign({},item);
            this.isNew = false ; 
        }
        $('#productModal').modal('show')
    },
    updateProduct(){
        let api = `${process.env.VUE_APP_API}/admin/product`;
        let httpmethod = 'post'
        const vm = this;
        if(!vm.isNew) {
            api = `${process.env.VUE_APP_API}/admin/product/${vm.tempProduct.id}`;
            httpmethod = 'put';
        }
        //為了要符合api的規格,所以tempProduct要做處理
        this.$http[httpmethod](api,{data:vm.tempProduct}).then((response) => {
                
                if(response.data.success) {
                    $('#productModal').modal('hide')
                    vm.getProducts();
                }else{
                    console.log('新增失敗')
                }
            // vm.products = response.data.products;
        })
    },
    opendelProductModal(item){
        console.log(item.id)
        this.tempProduct = Object.assign({},item); //避免物件傳參考所以用assign()重新建立一新的
        $('#delProductModal').modal('show')
        
    },
    deleteProduct(){
        let httpmethod = 'delete'
        const vm = this;
        let api = `${process.env.VUE_APP_API}/admin/product/${vm.tempProduct.id}`;
     
        //為了要符合api的規格,所以tempProduct要做處理
        this.$http[httpmethod](api,{data:vm.tempProduct}).then((response) => {
                if(response.data.success) {
                    console.log(response.data)
                    $('#delProductModal').modal('hide')
                    vm.getProducts();
                }else{
                    console.log('刪除失敗')
                }
        })
    },
    uploadFile() {
        // 處理上傳圖片用formData
        const uploadFile = this.$refs.files.files[0];
        const vm = this;
        console.log(uploadFile)
        const formData = new FormData(); //參考https://developer.mozilla.org/en-US/docs/Web/API/FormData/FormData
        formData.append('file-to-upload',uploadFile)
        const url = `${process.env.VUE_APP_API}/admin/upload`;
        vm.status.fileUploading = true ; 
        this.$http.post(url,formData,{
            headers: {
                'Content-Type':'multipart/form-data'
            }
        }).then((response)=>{
             vm.status.fileUploading = false ; 
            if(response.data.success) {
                vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl); 
            }else {
                this.$bus.$emit('messsage:push', response.data.message, 'danger');
            }
        })
       
    }


  },
  created(){
      this.getProducts();
      this.$bus.$emit('message:push','message','success');
  }
};
</script>